.main_portion{
  background-color: rgb(238, 238, 236);
  border-radius: 10px;
  padding:25px 15px;
  box-shadow: 0 0 10px grey ;
}

a{
    text-decoration: none;
    color: lightslategray;
}

a:hover{
    text-decoration: none;
    color: black;
}


.sidebar_avatar{
    height: 250px;
    width: 250px;
    object-fit: contain;
    border-radius: 20px;
}

.sidebar{
  background-color: rgb(212, 217, 221);
  height: 100%;
  border-radius: 10px;
  padding:25px 15px;
  box-shadow: 0 0 10px grey ;
  text-align: center;
}

.sidebar_icon{
    width: 30px;
    height: 30px;
    margin-left: 18px;
    margin-right: 10px;
}

.sidebar_name{
    font-family:'Times New Roman', Times, serif;
    margin: 20px 0;
    font-size: 1.8rem;
    

}

.sidebar_name{
    color: rgb(14, 13, 13);
   
}
.sidebar_item{
    margin-bottom: 6px;
    margin-top:13px;
    height: 40px;
    font-size: 1.3rem;
    border-radius: 30px;
    

}




.sidebar_title{
    background-color: #bdc3c7;
    border-radius: 10px;
}

.contact{
    background-color:#bdc3c7;
    padding: 60px 2px;
    margin-top: 20px 0;
    margin: 20px -15px;
    border-radius: 10px;
   
   
}

.sidebar_email{
    background-color: cadetblue;
    border-radius: 50px;
    margin-top: 80px;
    color: #fff;
    margin: 30px 25px 0px;
    cursor: pointer;
    transition: all 3s linear;
    

}

.sidebar_email:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.sidebar_github:hover,
.sidebar_resume:hover{
    background-color: #bdc3c7;
    color: black;
    border-radius: 10px;

}

@media(max-width:991px){
    .main_portion{
        margin: 30px 0;

    }
}
.navbar__items {
    display: flex;
}

.navbar__active {
    font-size: 2rem;
    font-weight: 700;
    margin-left: 20px;
    color: #354358;
    border-bottom: 4px solid #c31432;
}

.navbar__item {
    margin-right: 20px;
    text-decoration: none;
    color: #354358;
    font-size: 1.5rem;
    cursor: pointer;
}

.navbar__item:hover {
    background: -webkit-linear-gradient(#0F2027, #203A43,#2C5364);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent
}
.projects {
    height: 70vh;
    padding-top: 30px;
    overflow-y: auto;
}

.projectCard__wrapper {
    margin: 10px 0;
    width: 100%;
    height: 100%;
    min-height: 160px;
}

.projectCard__image {
    width: 100%;
    height: 70%;
    border-radius: 10px;
    border: 1px solid #0e0f0f;
    transition: -webkit-transform 0.3s linear;
    transition: transform 0.3s linear;
    transition: transform 0.3s linear, -webkit-transform 0.3s linear;
}

.projectCard__image:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.projectCard__icon {
    width: 35px;
    height: 35px;
    margin-right: 5px;
}

.projectCard__title {
    margin-top: 15px;
    font: size 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.projects__navbar {
    display: flex;
    margin-bottom: 5px;
}

.projects__navbar>div {
    margin: 0 5px;
    font-size: 1rem;
    cursor: pointer;
}
.resume {
    padding-top: 30px;
}

.resume-card__heading,
.resume-language__heading {
    margin-left: 0px;
    font-size: 1.8rem;
    font-weight: 700;
    color: #354538;
   
}

.resume-card__body {
    margin-top: 30px;
    
}

.resume-languages{
    margin-top: 50px;
}

.resume-card__title {
    font-weight: 700;
}

.resume-card__name {
    font-size: 16px;
    
}

.resume-card__details {
    font-size: 2rem;
    padding-top: 5px;
}

/* bar */
.bar {
    width: 90%;
   
    border-radius: 20px;
    margin-bottom: 12px;

}

.bar__icon {
    width: 20px;
    height: 20px;
    margin-left: 1px;
    color: white;
    
}

.bar__wrapper {
    background-image: linear-gradient(90deg, #0F2027, #203A43,#2C5364);
    border-radius: 5px;
    padding: 4px 14px;
    color: #fff;
    font-size: 1rem;
    
    
}
